<template>
	<view class="container" v-if="isshow">
		<watermark></watermark>
		<personList :personList="personList" :pageIndex="index"></personList>
	</view>
</template>

<script>
	import personList from '@/pages/salemanVisual/needDeal/components/personList.vue';
	import httpUtils from '@/utils/httpUtils.js';
	import Tools from '@/utils/tools.js';
	import watermark from '@/components/watermark.vue';
	export default {
		data() {
			return {
				index: '',
				isshow: false,
				personList: [],
				etype: 0, //0第一次加载 1查询 2下拉刷新 3上拉加载更多
				page: {
					pageNum: 1,
					pageSize: 10
				},
				totalPages: 0, // 总页数
				loading: false,
			}
		},
		onLoad(options) {
			this.index = options.index
			switch (options.index) {
				case '0':
					uni.setNavigationBarTitle({
						title: '预约今日到访'
					})
					break
				case '1':
					uni.setNavigationBarTitle({
						title: '今日待回访'
					})
					break
				case '2':
					uni.setNavigationBarTitle({
						title: '三日内逾期'
					})
					break
				case '3':
					uni.setNavigationBarTitle({
						title: '逾期未跟进'
					})
					break
				case '4':
					uni.setNavigationBarTitle({
						title: '公共客户'
					})
					break
			}
			this._getList(options.index)
		},
		methods: {
			// 获取列表
			_getList() {
				let that = this,
					postData = {
						module: parseInt(that.index),
					}
				Tools.showLoading('加载中')
				httpUtils.sendHttpRequest('2028', Object.assign({}, postData, that.page), function(res) {
					Tools.hideLoading()
					uni.hideNavigationBarLoading()
					if (res.data.code == '0') {
						that.isshow = true
						that.personList = that.etype == 3 ? that.personList.concat(res.data.data) : res.data.data
						that.totalPages = res.data.totalPages
						if (that.loading) {
							that.loading = false
							uni.stopPullDownRefresh()
						}
					}
				})
			},
			// 下拉刷新
			onPullDownRefresh() {
				this.page.pageNum = 1
				this.etype = 2
				if (!this.loading) {
					this.loading = true
					this._getList()
				}
			},
			// 上拉加载
			onReachBottom() {
				if (this.totalPages > this.page.pageNum) {
					this.etype = 3
					this.page.pageNum++
					this._getList()
				}
			},
		},
		components: {
			personList, watermark
		}
	}
</script>

<style lang="scss">
	page{
		height: 100%;
		background-color: #f6f6f6;
	}
</style>
